import React from 'react'
import { graphql, Link } from 'gatsby'
import Helmet from 'react-helmet'
import styled from '@emotion/styled'
import PropTypes from 'prop-types'
import Layout from '../layouts/'
import Container from '../layouts/Container'
import Content from '../layouts/Content'
import Header from '../layouts/Header'
import SEO from '../components/SEO'
import TagsBlock from '../components/TagsBlock'
import site from '../../config/site'
import { Disqus, CommentCount } from 'gatsby-plugin-disqus'

import {
  FacebookShareButton,
  FacebookIcon,
  LinkedinShareButton,
  LinkedinIcon,
  TwitterShareButton,
  TwitterIcon
} from 'react-share'

const SuggestionBar = styled.div`
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  background: ${props => props.theme.colors.background};
  box-shadow: ${props => props.theme.shadows.post.default};
`
const ListSuggestionBar = styled.div`
  background: ${props => props.theme.colors.background};
  box-shadow: ${props => props.theme.shadows.post.default};
`
const PostSuggestion = styled.div`
  display: flex;
  align-items: center;
  margin: 1rem 3rem 0 3rem;
  a {
    color: ${props => props.theme.colors.text.base};
  }
`
const ShareButtons = styled.div`
  display: flex;
  flex-wrap: nowrap;
  justify-content: start;
  background: ${props => props.theme.colors.background};
`

const Post = ({ data, pageContext }) => {
  const { next, prev, relatedPosts, latestPosts } = pageContext
  const post = data.markdownRemark
  const image = post.frontmatter.cover.childImageSharp.fluid
  const title = post.frontmatter.title
  const date = post.frontmatter.date
  const html = post.html
  const disqusConfig = {
    url: `${site.siteUrl + post.frontmatter.path}`,
    identifier: post.id,
    title: post.title
  }

  // 関連記事
  const relatedList = []
  for (var i in relatedPosts) {
    const relatedPost = relatedPosts[i]
    relatedList.push(
      <PostSuggestion>
        {relatedPost && (
          <Link to={relatedPost.frontmatter.path}>
            <h3>{relatedPost.frontmatter.title}</h3>
          </Link>
        )}
      </PostSuggestion>
    )
  }

  // 直近
  const latestList = []
  for (i in latestPosts) {
    const latestPost = latestPosts[i]
    latestList.push(
      <PostSuggestion>
        {latestPost && (
          <Link to={`posts${latestPost.frontmatter.path}`}>
            <h3>{latestPost.frontmatter.title}</h3>
          </Link>
        )}
      </PostSuggestion>
    )
  }

  return (
    <Layout>
      <Helmet>
        <script
          type='text/javascript'
          src='//b.st-hatena.com/js/bookmark_button.js'
          charset='utf-8'
          async='async'
        />
      </Helmet>
      <SEO
        title={title}
        description={post.frontmatter.description || post.excerpt || ' '}
        image={image}
        pathname={`${site.url}posts${post.frontmatter.path}`}
        article
      />
      <Header title={title} date={date} cover={image} />
      <Container>
        <Content input={html} />
        <TagsBlock list={post.frontmatter.tags || []} />
        <ShareButtons>
          <FacebookShareButton
            url={`${site.url}posts${post.frontmatter.path}`}
            className='ShareButton'
          >
            <FacebookIcon size={42} round />
          </FacebookShareButton>

          <LinkedinShareButton
            url={`${site.url}posts${post.frontmatter.path}`}
            className='ShareButton'
          >
            <LinkedinIcon title={title} size={42} round />
          </LinkedinShareButton>

          <TwitterShareButton
            title={title}
            via='@Shrike_A25'
            url={`${site.url}posts${post.frontmatter.path}`}
            className='ShareButton'
          >
            <TwitterIcon size={42} round />
          </TwitterShareButton>
          <a
            href='http://b.hatena.ne.jp/entry/'
            className='hatena-bookmark-button'
            data-hatena-bookmark-layout='vertical-normal'
            data-hatena-bookmark-lang='ja'
            title='このエントリーをはてなブックマークに追加'
          >
            <img
              src='//b.st-hatena.com/images/entry-button/button-only@2x.png'
              alt='このエントリーをはてなブックマークに追加'
              width='42'
              height='42'
              style={{ border: 'none' }}
            />
          </a>
        </ShareButtons>
      </Container>
      <SuggestionBar>
        <PostSuggestion>
          {prev && (
            <Link to={`posts${prev.frontmatter.path}`}>
              Previous
              <h3>{prev.frontmatter.title}</h3>
            </Link>
          )}
        </PostSuggestion>
        <PostSuggestion>
          {next && (
            <Link to={`posts${next.frontmatter.path}`}>
              Next
              <h3>{next.frontmatter.title}</h3>
            </Link>
          )}
        </PostSuggestion>
      </SuggestionBar>
      <ListSuggestionBar>
        関連記事
        {relatedList}
      </ListSuggestionBar>
      <ListSuggestionBar>
        最近投稿した記事
        {latestList}
      </ListSuggestionBar>
      <CommentCount config={disqusConfig} placeholder='...' />
      <Disqus config={disqusConfig} />
    </Layout>
  )
}

export default Post

Post.propTypes = {
  pageContext: PropTypes.shape({
    prev: PropTypes.object,
    next: PropTypes.object,
    relatedPosts: PropTypes.array,
    latestPosts: PropTypes.array
  }).isRequired,
  data: PropTypes.object.isRequired
}

export const query = graphql`
  query($pathSlug: String!) {
    markdownRemark(frontmatter: { path: { eq: $pathSlug } }) {
      html
      frontmatter {
        date
        title
        path
        tags
        description
        cover {
          childImageSharp {
            fluid(
              maxWidth: 1920
              quality: 90
              duotone: { highlight: "#386eee", shadow: "#2323be", opacity: 60 }
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }
            resize(width: 1200, quality: 90) {
              src
            }
          }
        }
      }
    }
  }
`
